.home-tab img{
    width: 100%;
}
.circle ul {
    display: block;
    margin: auto;
    margin-top:-25px;
    width: 280px;
}
.circle li{
    padding:5px;
    background-color: rgba(0, 0, 0, 0.3);
    width: 0;
    border-radius: 100%;
    display: inline-block;
    margin: 0 10px 0 10px;
}
.home-tab ul .circle-selected {
    background-color: rgba(0,0,0,.8);
}
.home-tab-head {
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 10px;
}
.home-tab-head input{
    background-color: rgba(191, 231, 243, 0.4);
    border-radius: 20px;
    height: 40px;
    padding: 0 10px 0 10px;
    font-size: 20px;
    position: absolute;
    left: 70px;
    top:20px
}
.home-tab-head i {
    padding-left: 10px;
    font-size: 50px;
    color: rgba(0, 0, 0, 0.8);
}
.search-items {
    background-color: rgba(246, 102, 123,.1);
}
.items1 {
    display: flex;
}
.items1 img{
    display: inline-block;
    width: 5px;
    flex: 1;
    margin: 10px;
}
.items2 {
    display: flex;
}
.items2 img{
    display: inline-block;
    width: 50px;
    flex: 1;
    margin: 10px;
}