.home-foot{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100px;
    background-color: rgba(122, 200, 231, 0.4);
    width: 100%;
    display: flex;
    display: -webkit-flex; 
}
.link{
    flex:1;
    display: inline-block;
    font-size: 10px;
    color: rgb(0, 0, 0);
    text-align: center;
}
.link i {
    color:rgb(3, 1, 1);
    font-size: 30px;
    display: block;
    padding:10px 0 10px 0;
}
.link span {
    text-align: center;
    font-size: 18px;
}
div .select {
    color:blue;
}